<template>
  <div>
    <h4>Key Metrics</h4>
    <div class="container">
        <canvas id="typeChart"></canvas>
    </div>
  </div>
</template>

<script>
import db from './firebaseInit.js'

export default {
    name: 'metrics',
    data() {
        return {
            employees: [],
            chartArray: [],
            loaded: false
        }
    },
    methods: {
        createChart(chartId, chartData) {
            const ctx = document.getElementById(chartId);
            const myChart = new Chart(ctx, {
            type: 'doughnut',
            data: {
              labels: ['Full-time', "Contract"],
              datasets: [
                {
                  data: chartData,
                  backgroundColor: ['lightblue', 'orange'] 
                },
              ]
            },
            options: {
              title: {
                  display: true,
                  position: 'top',
                  text: 'Full-time vs. Contract employees',
                  fontSize: 20,
                  fontColor: 'black',
                  padding: 20
              },
              legend: {
                display: true,
                position: 'bottom',
                labels: {
                    fontColor: 'black'
                }
              },
              responsive: true,
              rotation: (-0.5*Math.PI) + (144/180 * Math.PI),
              scales: {
                xAxes: [{
                  gridLines: {
                      display:false,
                      drawBorder: false
                  },
                  ticks: {
                          display: false //this will remove only the label
                  }
                }],
                yAxes: [{
                  gridLines: {
                      display:false,
                      drawBorder: false
                  },
                  ticks: {
                          display: false //this will remove only the label
                  } 
                }]
              }
            }
        });
      }
    },
    created () {
        db.collection('employees').orderBy('dept').get()
            .then(querySnapshot => {
            querySnapshot.forEach(doc => {
                const data = {
                    'id': doc.id,
                    'employee_id': doc.data().employee_id,
                    'name': doc.data().name,
                    'dept': doc.data().dept,
                    'position': doc.data().position,
                    'type': doc.data().type
                }
                this.employees.push(data)
            })
        })
        .then(() => {
          this.chartArray.push(this.employees.filter(obj => obj.type === 'full-time').length)
          this.chartArray.push(this.employees.filter(obj => obj.type === 'contract').length)
        })
    },
    mounted() {
        this.createChart('typeChart', this.chartArray); 
    },
}

Chart.defaults.global.defaultFontFamily = "Roboto"
</script>


